import React, { lazy, Suspense } from 'react';

import {
  BrowserRouter, Switch, Route, Redirect, HashRouter,
} from 'react-router-dom';

const MainPage = lazy(() => import('../Pages/MainPage/MainPage'));
const UseTermsPrivacyPolicy = lazy(() => import('../Pages/UseTermsPrivacyPolicy/UseTermsPrivacyPolicy'));

function Routes() : JSX.Element {
  return (
    <HashRouter basename='/'>
      <Suspense fallback={() => <div />}>
        <Switch>
          <Route path="/" exact component={MainPage} />
          <Route path="/terms" component={UseTermsPrivacyPolicy} />
        </Switch>
      </Suspense>
    </HashRouter>
  );
}

export default Routes;
